  cursor: pointer;
  transition: 0.2s;
  user-select: none;
}
.pricing-table_term-switch__slider::before {
  position: absolute;
  bottom: 1px;
  left: 1px;
  width: 22px;
  height: 22px;
  background-color: #0075dd;
  border-radius: 50%;
  transition: 0.2s;
  content: "";
}
.pricing-table_term-switch__checkbox {
  width: 0;
