  box-shadow: 0 0 0 2px #0075dd;
}
.pricing-table_term-switch::before, .pricing-table_term-switch::after {
  position: absolute;
  width: 105px;
  color: #6a7f8e;
  font-weight: 500;
  font-size: 0.8125rem;
  line-height: calc( 22 / 13 );
  text-transform: uppercase;
  content: attr(data-monthly);
